import Vue from './plugins/vue'
import iSlider from './plugins/iSlider'
import { Util } from '@elegy7/spare'
import './style/core.less'
import './style/item.less'
import Draw from './draw'
const HOMEURI = './static'
let myslider
new Vue({
    el: '#app',
    data() {
        const iHeight = window.innerHeight > 960 ? 960 : window.innerHeight
        const iWidth = window.innerWidth > 640 ? 640 : window.innerWidth
        let scaleX = iWidth / 320,
            scaleY = iHeight / 480,
            query = Util.getRequest()
        if (query.showMenu) {
            scaleX = 1
            scaleY = 1
        }
        return {
            app: {},
            showMenu: query.showMenu,
            title: '',
            audioStatus: '',
            pages: [],
            imgs: [],
            precent: 0,
            scaleX,
            scaleY,
            query,
            HOMEURI,
            draw: require('./assets/images/draw.jpg'),
            drawmask: require('./assets/images/drawmask.jpg')
        }
    },
    methods: {
        initApp() {
            const self = this
            fetch(`${HOMEURI}/app.json`)
                //fetch(`${HOMEURI}/api/app?showId=${this.query.showId}`)
                .then(res => res.json())
                .then(data => {
                    data = data.result
                    document.querySelector('title').innerHTML = data.title
                    self.app = data
                    self.pages = data.pages
                    self.imgs = data.pages.map(item => {
                        return self.HOMEURI + item.src
                    })
                    this.initAudio()
                    this.initItem()
                })
        },
        initAudio() {
            let audio = new Audio()
            audio.onloadedmetadata = () => {
                this.audioStatus = 'pause'
            }
            audio.src = this.HOMEURI + this.app.musicUrl
        },
        initItem() {
            const self = this
            fetch(`${HOMEURI}/item.json`)
                //fetch(`${HOMEURI}/api/app/item?showId=${this.query.showId}`)
                .then(res => res.json())
                .then(data => {
                    self.pages.forEach(page => {
                        data.result.forEach(item => {
                            if (page.pageIndex == item.pageIndex) {
                                page.items ? page.items.push(item) : (page.items = [item])
                            }
                        })
                    })
                    self.$forceUpdate()
                    self.$nextTick(() => {
                        self.initSlider()
                    })
                })
        },
        initSlider() {
            const self = this
            //iSlider
            myslider = new iSlider({
                wrap: '#wrap',
                item: '.page',
                loadingImgs: self.imgs,
                lastLocate: !self.showMenu,
                onslide(index) {
                    if (index == 7) {
                        document.getElementById('J_joinNum').innerHTML = parseInt(Math.random() * 10000)
                    }
                },
                onloading(index) {
                    self.precent = ((index / (self.imgs.length + 1)).toFixed(2) * 100).toFixed(0)
                }
            })
        },
        prev() {
            myslider.prev()
        },
        next() {
            myslider.next()
        },
        //
        playAudio() {
            const audio = document.querySelector('#m_audio')
            audio.play()
            this.audioStatus = 'playing'
        },
        pauseAudio() {
            const audio = document.querySelector('#m_audio')
            audio.pause()
            this.audioStatus = 'pause'
        },
        initDraw() {
            if (this.app.drawModal) {
                Draw.init('#itemDraw', () => {
                    let $page = document.querySelector('.play')
                    $page.className = $page.className.replace(' play', '')
                    setTimeout(() => {
                        $page.className += ' play'
                        this.audioStatus == 'pause' && this.playAudio()
                    }, 300)
                })
            }
        }
    },
    created() {
        this.initApp()
    },
    mounted() {
        this.initDraw()
    }
})
