var draw = {
    id: '',
    backimage: '',
    canvas: '',
    main: '',
    _moveY: '',
    _moveX: '',
    _heightPercent: 1,
    _widthPercent: 1,
    callback: function() {},
    init: function(id, callback) {
        var itemDraw = document.querySelector(id),
            imgBackground = itemDraw.getAttribute('data-backbround'),
            imgMask = itemDraw.getAttribute('data-mask')
        //loading
        draw.id = id
        draw.callback = callback || function() {}
        draw.backimage = { url: imgMask, img: null }
        draw.main = itemDraw
        draw._on()
        draw.backimage.img = document.createElement('img')
        draw.backimage.img.addEventListener('load', draw._setupCanvases, true)
        draw.backimage.img.src = draw.backimage.url
        draw.backimage.img.onload = function() {
            itemDraw.style.position = 'absolute'
            itemDraw.style.zIndex = 999
            itemDraw.style.top = 0
            itemDraw.style.height = '100%'
            itemDraw.style.width = '100%'
            itemDraw.style.backgroundImage = 'url(' + imgBackground + ')'
            itemDraw.style.backgroundSize = '100% 100%'
            itemDraw.className += ' auto-draw'
        }
        document.body.addEventListener(
            'touchmove',
            function(e) {
                e.preventDefault()
            },
            false
        )
    },
    _on: function() {
        draw.main.addEventListener('touchstart', draw._touchstart)
        draw.main.addEventListener('touchmove', draw._touchmove)
        draw.main.addEventListener('touchend', draw._touchend)
    },
    // 
    _scratch: function(canv, x, y, fresh) {
        canv = draw.main
        var ctx = canv.getContext('2d')
        // , ...
        ctx.lineWidth = 45 * draw._widthPercent
        ctx.lineCap = ctx.lineJoin = 'round'
        if (fresh) {
            ctx.beginPath()
            //  x  0.01
            ctx.moveTo(x + 0.01, y)
        }
        ctx.lineTo(x, y)
        ctx.stroke()
    },
    _setupCanvases: function() {
        var c = document.querySelector(draw.id)
        // 
        c.width = draw.backimage.img.width
        c.height = draw.backimage.img.height

        var h = window.outerHeight || 640
        var w = window.outerWidth || 320
        /* var _type = util.ua();
        if(_type['android']&& h >c.height)h = h-c.height;
        if(_type['android']&& w >c.width)w = w-c.width; */
        draw._heightPercent = c.height / h
        draw._widthPercent = c.width / w

        var mainctx = draw.main.getContext('2d')
        //
        mainctx.fillStyle = 'transparent'
        mainctx.fillRect(0, 0, c.width, c.height)
        mainctx.drawImage(draw.backimage.img, 0, 0)
        mainctx.globalCompositeOperation = 'destination-out'
    },
    _touchstart: function(ev) {
        //Y
        if (ev.type == 'touchstart') {
            draw._moveY = ev.touches[0].pageY * draw._heightPercent
            draw._moveX = ev.touches[0].pageX * draw._widthPercent
            draw._scratch(draw.main, draw._moveX, draw._moveY, true)
        }
    },
    _touchmove: function(ev) {
        //Ymove()
        if (ev.type == 'touchmove') {
            draw._moveY = ev.touches[0].pageY * draw._heightPercent
            draw._moveX = ev.touches[0].pageX * draw._widthPercent
            draw._scratch(draw.main, draw._moveX, draw._moveY, false)
        }
    },
    _touchend: function(ev) {
        if (ev.type == 'touchend') {
            if (draw.main.style.display != 'none') draw._clear()
        }
    },
    _clear: function() {
        var w = draw.backimage.img.width
        var h = draw.backimage.img.height
        var ctx = draw.main.getContext('2d')
        var data = ctx.getImageData(0, 0, w, h).data
        for (var i = 0, j = 0; i < data.length; i += 4) {
            if (
                data[i] &&
                data[i + 1] &&
                data[i + 2] &&
                data[i + 3] &&
                data[i + 4]
            ) {
                j++
            }
        }
        if (j <= w * h * 0.5) {
            draw.callback()
            $(draw.main)[0].style['opacity'] = 1
            draw.main.style.opacity = 0
            setTimeout(function() {
                draw.main.style.display = 'none'
            }, 1000)
        }
    }
}
export default draw
