var Wechat = {
    init: function (config, callback) {
        // http://res.wx.qq.com/open/js/jweixin-1.2.0.js
        callback = callback || function () {}
        var jsApiList = config['jsApiList'] || ['hideOptionMenu', 'showOptionMenu', 'onMenuShareAppMessage', 'onMenuShareTimeline', 'onMenuShareQQ', 'onMenuShareWeibo'],
            result = config['token']

        wx.config({
            debug: config['debug'], // ,apialertpclogpc
            appId: result.appId, // 
            timestamp: result.timestamp, // 
            nonceStr: result.nonceStr, // 
            signature: result.signature, // 1
            jsApiList: jsApiList // JSJS2
        })

        wx.ready(function () {
            callback()
        })
    },
    initShare: function (config) {
        //   
        wx.onMenuShareAppMessage({
            title: config['title'] || '',
            desc: config['desc'] || '',
            link: config['link'] || '',
            imgUrl: config['imgUrl'] || '',
            trigger: function (res) {
                var callback = config['trigger'] || function () {}
                callback();
            },
            success: function (res) {
                var callback = config['success'] || function () {}
                callback();
            },
            cancel: function (res) {
                var callback = config['cancel'] || function () {}
                callback();
            },
            fail: function (res) {
                var callback = config['fail'] || function () {}
                callback();
            }
        });
        //   
        wx.onMenuShareTimeline({
            title: config['desc'] || '',
            link: config['link'] || '',
            imgUrl: config['imgUrl'] || '',
            success: function (res) {
                var callback = config['success'] || function () {}
                callback();
            },
            cancel: function (res) {
                var callback = config['cancel'] || function () {}
                callback();
            }
        });
        //   
        wx.onMenuShareWeibo({
            title: config['title'] || '',
            desc: config['desc'] || '',
            link: config['link'] || '',
            imgUrl: config['imgUrl'] || '',
            success: function (res) {
                var callback = config['success'] || function () {}
                callback();
            },
            cancel: function (res) {
                var callback = config['cancel'] || function () {}
                callback();
            }
        });
        // QQ
        wx.onMenuShareQQ({
            title: config['title'] || '',
            desc: config['desc'] || '',
            link: config['link'] || '',
            imgUrl: config['imgUrl'] || '',
            success: function (res) {
                var callback = config['success'] || function () {}
                callback();
            },
            cancel: function (res) {
                var callback = config['cancel'] || function () {}
                callback();
            }
        });
    }
}
export var {
    init,
    initShare
} = Wechat
